.comments {
  padding: 30px 26px 35px;
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 2px 8px 0 rgba(0, 0, 0, 0.11);
  &__title {
    font-size: 1.6rem;
    font-weight: 700;
    margin-bottom: 28px; }
  &__form {
    width: 100%;
    position: relative;
    margin-bottom: 35px; }
  &__textarea {
    display: block;
    width: 100%;
    height: 57px;
    padding: 17px 20px;
    position: relative;
    font-size: 1.6rem;
    line-height: 1.2;
    resize: none;
    overflow: hidden; }
  &__textarea:focus {
    height: 150px; }
  &__textarea:focus .comments__submit {
    bottom: 8px; }
  &__submit {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    right: 20px;
    top: 8px;
    background-color: #fff;
    border: none; }
  &__submit img {
    width: 33px;
    height: 100%; }

  &__list {
    width: 100%; }
  &__item {
    width: 100%;
    position: relative;
    &-info {
      padding-bottom: 15px;
      margin-bottom: 30px;
      border-bottom: 1px solid #e8e8e8; }
    &-header {
      display: flex;
      justify-content: space-between;
      align-items: bottom;
      margin-bottom: 15px; }
    &-author {
      display: flex;
      justify-content: flex-start; }
    &-img {
      width: 20px;
      height: 20px;
      margin-right: 8px; }
    &-name {
      margin: 0;
      padding-top: 4px;
      margin-right: 9px;
      font-size: 1.6rem;
      line-height: 1.2;
      color: #888888;
      font-weight: 700; }
    &-date {
      margin: 0;
      padding-top: 5px;
      font-size: 1.4rem;
      line-height: 1.2;
      color: #888888; }
    &-assessment {
      padding-top: 5px; }
    &-assessment a {
      text-decoration: none;
      font-size: 1.4rem;
      line-height: 1.2; }
    &-like {
      color: #4bb625;
      margin-right: 5px; }
    &-dislike {
      color: #ef3939; }
    &-text {
      font-size: 1.6rem;
      line-height: 1.2;
      color: #3e3e3e;
      margin-bottom: 10px; }
    &-btn {
      font-size: 1.4rem;
      line-height: 1.2;
      color: #b5b5b5;
      text-decoration: none; }
    &-reply {
      padding-left: 30px; }
    &-opener {
      display: block;
      content: '';
      position: absolute;
      width: 30px;
      height: 50px;
      left: -5px;
      background-image: url('img/svg/arrow-up.svg');
      background-repeat: no-repeat;
      background-size: 17px;
      background-position: center 8px; } }
  &__also a {
    color: #1d1d1d;
    font-size: 1.6rem;
    line-height: 1.2;
    text-decoration: none;
    border-bottom: 1px solid #888888; } }


